//@import "~swiper/css/bundle";
@import "~swiper/scss";

/** */
.swiper-container {
    margin-left: auto;
    margin-right: auto;
    position: relative;
    overflow: hidden;
    list-style: none;
    padding: 0;
    z-index: 1;
    display: block;
}

.swiper-marquee {
    .swiper-wrapper {
        transition-timing-function: linear !important;
        touch-action: manipulation;
    }
}
